
import React from "react";
import PropTypes from "prop-types";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  }
  getMovies = async () => {
    const { data: { data: { movies } } } = await axios.get("https://yts.mx/api/v2/list_movies.json?sort_by_rating");
    this.setState({ movies: movies, isLoading: false });
    console.log(movies);
  }
  componentDidMount() {
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state;
    console.log(isLoading, movies);
    return <section className="container">{isLoading ? <div className="loader"><span className="loader__text">Loading</span></div> : <div className="movies">{movies.map(movie => {
      console.log(movie);
      return <Movie key={movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} medium_cover_image={movie.medium_cover_image} genres={movie.genres} />
    })}</div>}</section>
  }
}

export default App;